@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

*,
*::after,
*::before{
  box-sizing: border-box;
  font-family: 'Lobster', cursive;
  font-weight: normal;
}

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to right, #36a7a7, rgb(45, 90, 153));
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1{
  text-align: center;
  padding: 1rem;
  font-weight: bold;
  /* font-family: 'Times New Roman', Times, serif; */
  background-color: #103D5D;
  color: white;
}

.content{
    /* display: grid; */
    /* justify-content: center; */
    align-content: center;
    /* width: 50%; */
    /* margin: 40px auto; */
    /* grid-template-columns: repeat(3, 100px); */
    /* grid-template-rows: minmax(1em , auto) repeat(5,1em); */
    box-shadow: 2px 2px 10px #333;
    border-radius: 10px;
    background-color: white;
}
.form{
  /* padding: 2rem; */
}


.align-right{
  text-align: right!important;
}


#root{
  display: inline;
}


.add-item {
  border-radius: 2px;
  border-top: 3px solid #103D5D;
  margin-right: 50px;
  background-color: rgb(237, 237, 237);
  padding: 3rem 1rem;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
  margin-bottom: 34px;  
  /* width: 25%; */
  margin-right: auto;
}


.display-item {
  width: 65%;
  margin-left: 50px;
}

.display-item button {
  margin-top: 20px;
  margin-left: auto;
  display: block;
}

.add-item form {
  width: 100%;
  overflow: hidden;
}

input {
  font-size: 22px;
  color: #000;
  padding: 18px 22px;
  font-size: 16px;
  margin-bottom: 17px;
  border: 0;
  display: block;
  width: 100%;  
}

ul {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

li {
  width: calc(33% - 10px);
  list-style-type: none;
  margin-right: 10px;
  background-color: rgb(237, 237, 237);
  color: #000;
  margin-bottom: 10px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
}

h2 {
  padding: 0;
  margin: 0;
  font-weight: 400;
}

li h3 {
  background-color: #103D5D;
  margin: 0;
  color: #fff;
  font-weight: 300;
  padding: 15px;
}

li p {
  padding: 15px;
}

form button {
  width: 100%;
  margin-top: 10px;
}

button {
  border-radius: 2px;
  min-width: 88px;
  background-color: #FA6900;
  cursor: pointer;
  border: 0;
  min-width: 120px;
  color: #fff;
  font-size: 14px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
  padding: 10px 6px;
}

.error{

  background-color: rgb(198, 46, 46);
  color: #ffffff;
  padding: 5px;
  border-radius: 5px;
  
}

.delete-button{
  text-align: right;
  cursor: pointer;
}

.card-title{
  display: block;
  background-color: #FA6900;
  padding-bottom: 5px;
  padding-top: 5px;
  color: white;
  
  
}

.icon{
  padding: 1rem;
  cursor: pointer;
  font-size: 2rem;
  
}

.card-title .text{
  text-align: right;
  font-weight: bold;
}

.card{
  background-color: #eee;
}

.profile{
  font-size: 10pt!important;
}

.profile span{
  cursor: pointer;
}

.profile img{
  width: 20%;
  border-radius: 50%;
}

.login{
  background-color: #6F80C1;
  /* width: 100%; */
  text-align: center!important;
  align-items: center;
  min-height: 20rem;

}

.hidden {
  display: none;
}
